import React, { Component } from 'react';
import Axios from 'axios';
import Style from './HomePage.css';
import { Header, Days, Hours } from '../';

class HomePage extends Component {
  constructor() {
    super();
    this.state = {
      isLoading: true,
      data: [],
      selectedDay: [],
      city: {},
    }
  }
  componentDidMount() {
    const url = 'https://api.openweathermap.org/data/2.5/forecast?q=gurgaon&mode=json&type=accurate&units=metric&appid=f8bf055299ecf93373c28105f784c9ae';
    Axios.get(url).then((res) => {
      this.setState({
        isLoading: false,
        city: res.data.city,
      });
      const list = res.data.list;
      const groupedData = [];
      list.forEach((item) => {
        const day = new Date(item.dt * 1000).getDay();
        const dayName = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'][day];
        const existedItem = groupedData.findIndex((groupedItem) => (groupedItem.day === dayName));
        if (existedItem < 0) {
          groupedData.push({ day: dayName, data: [item] });
        } else {
          groupedData[existedItem].data.push(item);
        }
      });
      this.setState({
        data: groupedData,
        selectedDay: groupedData[0],
      });
    });
  }

  onDayClick(item) {
    this.setState({
      selectedDay: item,
      selectedTime: item.data[0],
    });
  }

  render() {
    if (this.state.isLoading) {
      return (<div className='mainContainer'><div className="loadingContainer">Loading.....</div></div>);
    }
    const { city, selectedDay, data } = this.state;
    return (
      <div className='mainContainer'>
        {
          data.length ?
            <div>
              <Header selectedDay={selectedDay} cityName={city.name} />
              <Days selectedDay={selectedDay} data={data} onDayClick={(item) => this.onDayClick(item)} />
              <Hours selectedDay={selectedDay} />
            </div> :
            null
        }
      </div>
    );
  }
}

export default HomePage;



